import {
    mapState,
    mapMutations
} from 'vuex'
import uniIcon from "@/components/uni-icon.vue"
export default {
    components: {
        uniIcon
    },
    computed: mapState(['hasLogin', 'userHeadimg', 'userName']),
    data () {
        return {
            list: [{
                name: "修改密码",
                url: "/pages/mine/set-password/set-password",
                iconName: "icon-fapiaosel"
            },
            {
                name: "应用设置",
                url: "/pages/mine/application-settings/application-settings",
                iconName: "icon-mn_yonghuziliao_fill"
            },
            ]
        }
    },
    onLoad () {
        var info = uni.getStorageSync('info');
        if (info) {
            this.login();
            this.changeUserName(info.userName);
        }
    },
    methods: {
        ...mapMutations(['login', 'changeUserName']),
        go (url) {
            uni.navigateTo({
                url: url
            })
        },
        t () {
            this.login()
        }
    }
}
